.menu {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.close-menu,
.menu-icon--open {
  display: none;
}

.menu__link {
  font-family: $title-font;
  color: #fff;
  text-transform: uppercase;
  font-size: 24px;
}

.menu__icon {
  max-width: 64px;
  max-height: 96px;
  height: auto;
  fill: #fff;
}

.menu__item {
  width: 100%;
  padding: 15px 10px;
  transition: 0.3s;

  &:nth-of-type(odd) {
    background-color: rgba(#fff, 0.03)
  }

  &:hover,
  &:focus,
  &:active,
  &.active {
    background-color: $prime-color;

    & .menu__link {
      color: $text-color;
    }

    & .menu__icon {
      fill: $text-color;
    }
  }
}

#menu__toggle {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  font-family: $title-font;
  color: #fff;
  text-transform: uppercase;
  font-size: 24px;
  cursor: pointer;

  & p {
    margin-right: 20px;
    margin-bottom: 0;
  }
}

.menu__lang {
  transform: rotate(-90deg);
  display: flex;
  font-family: $title-font;
  color: #fff;
  font-size: 24px;
  justify-content: space-between;
  margin-top: 150px;
  margin-bottom: 60px;
}

.menu__lang-item {
  margin-right: 15px;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: 0.3s;

  &:hover,
  &:focus,
  &:active {
    border-color: $prime-color;
  }

  &.active {
    border-color: $prime-color;
    margin-left: 80px;
    position: relative;
    order: 2;

    &::after {
      content: "";
      position: absolute;
      top: 18px;
      left: -68px;
      width: 47px;
      height: 1px;
      background-color: rgba(#fff, 0.36);
    }
  }
}

.menu__social {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__social-phone {
  margin-bottom: 50px;
  position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 18px;
      left: -68px;
      width: 1px;
      height: 22px;
      background-color: rgba(#fff, 0.36);
    }
}

.social {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social__icon {
  width: 36px;
  height: 36px;
  fill: #fff;
  margin-top: 7px;
  transition: 0.3s;

  &:hover,
  &:focus,
  &:active {
    fill: $prime-color;
  }
}

.header-menu {
  list-style: none;
  padding-left: 0;
}

.header-menu__item {
  font-size: 72px;
  text-align: right;
}

.header-menu__item:hover,
.header-menu__item:focus,
.header-menu__item:active,
.header-menu__item.active {
  text-align: left;
}

.header-menu__link {
  color: #fff;
  font-family: $title-font;
  border-bottom: 6px solid transparent;
  opacity: 0.5;
}

.header-menu__item:nth-of-type(odd) .header-menu__link {
  color: #292324;
  text-shadow: #fff 1px 0px, #fff 1px 1px, #fff 0px 1px, #fff -1px 1px, #fff -1px 0px, #fff -1px -1px, #fff 0px -1px, #fff 1px -1px;
}

.header-menu__item:hover .header-menu__link,
.header-menu__item:focus .header-menu__link,
.header-menu__item:active .header-menu__link,
.header-menu__item.active .header-menu__link {
  opacity: 1;
  border-color: $prime-color;
}

.header-menu__item:nth-of-type(odd):hover .header-menu__link,
.header-menu__item:nth-of-type(odd):focus .header-menu__link,
.header-menu__item:nth-of-type(odd):active .header-menu__link,
.header-menu__item:nth-of-type(odd).active .header-menu__link {
  color: #fff;
  text-shadow: none;
}

.header-phones {
  position: absolute;
  top: 50px;
  left: 40px;

  & .header-phone {
    display: inline-block;
    vertical-align: bottom;
    margin-right: 30px;
  }

  & .title {
    font-weight: 500;
    font-size: 12px;
    color: rgba(#fff, 0.42);
    margin-bottom: 0;
  }

  & .phone {
    font-size: 23px;
    font-family: $title-font;
    margin-bottom: 15px;
    color: #fff;
    display: inline-block;
  }
}

.header__right.open {
  align-items: flex-end;
  padding: 50px 40px;

  & #menu__toggle img:first-of-type,
  & #menu__toggle p {
    display: none;
  }

  & .menu-icon--open {
    display: block;
  }

  & #menu__toggle {
    transform: rotate(0);

    &::before {
      content: "Закрыть";
      text-transform: uppercase;
      font-family: $title-font;
      font-size: 56px;
      padding-right: 30px;
      padding-left: 70px;
      background-image: url('../img/arrow-back.png'),
                        url('../img/arrow-back.png'),
                        url('../img/arrow-back.png');
      background-repeat: no-repeat;
      background-position: center left, center left 20px, center left 40px;
      background-size: 20px 40px;

    }
  }

  & .menu__lang {
    transform: rotate(0);
    margin-bottom: 0;
    margin-top: 50px;
  }

  & .menu__social {
    position: absolute;
    bottom: 50px;
    left: 40px;
  }
}

#left-menu__toggle {
  color: #fff;
  font-size: 17px;
  font-family: $title-font;

  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    width: 22px;
    height: 22px;
    background: url('../img/catalog.png') no-repeat center/contain;
    margin-right: 10px;
  }

  &.open {
    font-size: 0;

    &::after {
      content: "Закрыть";
      color: #fff;
      text-transform: uppercase;
      font-size: 17px;
      font-family: $title-font;
      vertical-align: bottom;
    }

    &::before {
      background-image: url('../img/cross.png');
    }
  }
}

@media (max-width: $mobile-width) {
  .menu,
  .menu__lang,
  .menu__social {
    display: none;
  }

  #menu__toggle {
    transform: rotate(0);
    font-size: 17px;

    & img {
      max-width: 22px;
      max-height: 22px;
    }
  }

  .header__right.open {
    padding: 0;
    width: auto;
    display: block;
    background: none;

    & .header-menu {
      position: absolute;
      top: 40px;
      width: 100vw;
      right: -20px;
      padding: 10px 20px 100px;
      backdrop-filter: blur(30px);
      background: rgba(0, 0, 0, 0.8) url('../img/bg-menu.png') no-repeat center/cover;
    }

    & .header-phones,
    & .menu__social {
      display: none !important;
    }

    & .menu__lang {
      display: flex;
      position: absolute;
      bottom: -480px;
      right: 50%;
    }
  }

  .header__right.open #menu__toggle::before {
    font-size: 17px;
    background: none;
    padding-right: 15px;
    padding-left: 0;
  }

  .header-menu__item {
    font-size: 48px;
  }
}
