.footer {
  margin-top: -180px;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  & .title {
    font-weight: 500;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.42);
    margin-bottom: 0;
  }

  & .phone {
    font-size: 23px;
    font-family: $title-font;
    margin-bottom: 15px;
    color: $text-color;
    display: inline-block;
  }
}

.footer__contacts {
  max-width: 165px;

  & h2 {
    font-size: 44px;
    margin: 0 0 20px;
  }

  & .phones {
    margin-bottom: 40px;
  }
}

.email {
  margin-bottom: 55px;

  & a {
    color: $text-color;
    font-family: $title-font;
    font-size: 23px;
    text-transform: uppercase;
  }
}

.footer__social {
  & .social__icon {
    fill: $text-color;
    margin-right: 15px;
    margin-bottom: 15px;

    &:hover,
    &:focus,
    &:active {
      fill: $prime-color;
    }
  }
}

.footer__menu {
  max-width: 430px;
}

.footer__form {
  & h3 {
    font-size: 44px;
    margin: 0 0 25px;
  }

  & .button--form  {
    margin: 25px 0 35px;
  }
}

.footer__menu {
  & h3 {
    font-size: 44px;
    margin: 0 0 35px;
  }
}

.menu-footer {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 0;
}

.menu-footer__item {
  width: 33%;
  font-family: $title-font;
  margin-bottom: 15px;
}

.menu-footer__link {
  color: $text-color;
  font-size: 24px;
  display: inline-block;
  width: 100%;

  &:hover,
  &:focus,
  &:active {
    color: $prime-color;
  }
}

.footer__map {
  width: 346px;
  max-width: 34%;
  background: #FFF;
  box-shadow: 0px 3.20915px 12.0343px rgba(0, 0, 0, 0.25);
  padding: 16px 17px 15px;
  margin-bottom: 20px;

  & .title {
    margin: 10px 0;
    color: $text-color;
  }

  & .value {
    font-weight: 700;
    font-size: 15px;
  }
}

.footer__cr {
  background: $prime-color;

  & .wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  & p {
    margin: 10px 0;
  }

  & a {
    text-decoration-line: underline;
    color: #000000;
    opacity: 0.3;

    &:hover,
    &:focus,
    &:active {
      opacity: 1;
    }
  }
}

@media (max-width: $mobile-width) {
  .footer {
    margin-top: 30px;
  }

  .footer__nav,
  .footer__cr .wrapper {
    justify-content: space-evenly;
    text-align: center;
  }

  .footer__menu {
    order: 2;
  }

  .footer__map {
    margin: 30px 0;
    max-width: 100%;
  }

  .menu-footer {
    text-align: left;
  }

  .menu-footer__link {
    font-size: 16px;
  }
}
