.top {
  padding: 50px 0;
}

.top__title {
  font-size: 44px;
  text-transform: uppercase;
  margin: 0 0 55px;

  &::after {
    content: "";
    width: 54px;
    height: 54px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
  }
}

.top--rent {

  & .top__title::after {
    background: url('../img/top-rent.png') no-repeat center/contain;
  }

  & .top__link:nth-of-type(1) .top__item {
    background-image: url('../img/top-1.jpg')
  }
  & .top__link:nth-of-type(2) .top__item {
    background-image: url('../img/top-2.jpg')
  }
  & .top__link:nth-of-type(3) .top__item {
    background-image: url('../img/top-3.jpg')
  }
  & .top__link:nth-of-type(4) .top__item {
    background-image: url('../img/top-4.jpg')
  }
}

.top--sell {
  background: $prime-color;
  box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.25);

  & .top__title::after {
    vertical-align: baseline;
    background: url('../img/top-sell.png') no-repeat center/contain;
  }

  & .top__link:nth-of-type(1) .top__item {
    background-image: url('../img/top-5.jpg')
  }
  & .top__link:nth-of-type(2) .top__item {
    background-image: url('../img/top-6.jpg')
  }
  & .top__link:nth-of-type(3) .top__item {
    background-image: url('../img/top-7.jpg')
  }
  & .top__link:nth-of-type(4) .top__item {
    background-image: url('../img/top-8.jpg')
  }
}

.top--newbuild {
  color: #fff;
  background: #212121;
  box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.25);

  & .top__title::after {
    background: url('../img/top-newbuild.png') no-repeat center/contain;
  }

  & .top__link:nth-of-type(1) .top__item {
    background-image: url('../img/top-9.jpg')
  }
  & .top__link:nth-of-type(2) .top__item {
    background-image: url('../img/top-10.jpg')
  }
  & .top__link:nth-of-type(3) .top__item {
    background-image: url('../img/top-11.jpg')
  }
  & .top__link:nth-of-type(4) .top__item {
    background-image: url('../img/top-12.jpg')
  }
}

.top__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.top__item {
  padding: 200px 15px 10px;
  color: #fff;
  background-color: $text-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 10px;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 8px 20px 40px rgba(#000, 0.3);
  }

  & .title {
    font-weight: 500;
    opacity: 0.7;
    font-size: 12px;
  }

  & .value {
    max-width: 205px;
    font-family: $title-font;
    font-size: 22px;
  }

  & .value--adres {
    font-size: 17px;
    padding-right: 25px;
    background: url('../img/location.png') no-repeat center right;
  }
}

.top__subtitle {
  font-size: 44px;
  margin: 30px 0 25px;
  text-transform: inherit;
}

@media (max-width: $mobile-width) {
  .top {
    padding: 30px 0;
  }

  .top__title {
    font-size: 28px;
    margin-bottom: 30px;

    &::after {
      width: 32px;
      height: 32px;
    }
  }

  .top__list {
    margin-bottom: 0;
  }

  .top__link {
    width: 47%;
    margin-bottom: 5px;
  }

  .top__item {
    padding: 30px 5px 0;
    margin-bottom: 0;

    & .title {
      font-size: 9px;
      margin-bottom: 5px;
    }

    & .value {
      font-size: 14px;
    }

    & .value--adres {
      background: none;
    }
  }

  .top__subtitle {
    font-size: 28px;
  }
}
