/*Шрифты*/

@font-face {
  font-family: "DINCondensed";
  src: url("../fonts/DINCondensed-Regular.woff") format("woff"),
    url("../fonts/DINCondensed-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff") format("woff"),
    url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff") format("woff"),
    url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-ExtraBold.woff") format("woff"),
    url("../fonts/Montserrat-ExtraBold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

/*Переменные*/
$regular-font: "Montserrat", "PT Sans", sans-serif;
$title-font: "DINCondensed", sans-serif;
$prime-color: #C77B2B;
$text-color: #000000;
$bg-color: #E5E5E5;
$desktop-width: 1160px;
$mobile-width: 850px;

.wrapper {
  max-width: $desktop-width;
  padding: 0 64px;
  margin: 0 auto;
}

.hidden {
  display: none;
  font-size: 0;
  overflow: hidden;
  height: 0;
  width: 0;
}

@media (max-width: $mobile-width) {
  .wrapper {
    padding: 0 20px;
  }
}
