.popap {
  position: fixed;
  background-color: #fff;
  max-height: 100%;
  max-width: 100%;
  width: 1190px;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  z-index: 1010;
  padding: 150px 80px 10px;
  background-color: $prime-color;
  box-shadow: 0px 8px 20px rgba(123, 122, 122, 0.25);
  display: none;
}

.popap__close {
  position: absolute;
  top: 40px;
  right: 80px;
  color: $prime-color;
  text-shadow: #fff 1px 0px, #fff 1px 1px, #fff 0px 1px, #fff -1px 1px, #fff -1px 0px, #fff -1px -1px, #fff 0px -1px, #fff 1px -1px;
  font-size: 80px;
  text-transform: uppercase;
  padding-left: 40px;
  background: url('../img/arrow-back.png') no-repeat center left/auto 60px;
  font-family: $title-font;
  opacity: 0.7;
  cursor: pointer;
}

.popap__form {
  width: 345px;
  max-width: 35%;
  color: #fff;

  & h2 {
    font-size: 44px;
    text-transform: uppercase;
    margin: 0 0 25px;
  }
}

.popap__image {
  float: right;
  max-width: 50%;
}

.popap__cr {
  color: $text-color;
  text-align: center;
  margin-top: 100px;
}

@media (max-width: $mobile-width) {
  .popap {
    padding: 80px 20px 30px;
    overflow-y: scroll;
  }

  .popap__close {
    top: 10px;
    right: 20px;
    font-size: 50px;
    background-size: auto 40px;
  }

  .popap__image {
    float: none;
    max-width: 100%;
    margin: 0 auto 20px;
    text-align: center;
  }

  .popap__form {
    max-width: 100%;
    margin: 0 auto 20px;

    & h2 {
      font-size: 37px;
      margin: 0 0 15px;
    }
  }

  .popap__cr {
    display: none;
  }
}
