.callback {
  background: url('../img/callback-bg.png') no-repeat center/100% auto;
  padding: 480px 0 450px;
  margin-top: -595px;

  & .wrapper {
    display: flex;
    justify-content: space-evenly;
  }
}

.callback__text {
  max-width: 490px;
  color: #fff;

  & h2 {
    font-size: 44px;
    margin: 0 0 20px;
  }

  .accent {
    color: $text-color;
    text-decoration: underline;
  }
}

.callback__form {
  max-width: 340px;
  margin-top: 65px;
  color: #fff;

  & h3 {
    font-size: 44px;
  }
}

.form__input {
  width: 100%;
  height: 47px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.38);
  backdrop-filter: blur(30px);
  border: none;
  margin-bottom: 20px;
  color: #fff;
}

.form__input::placeholder {
  color: rgba(#fff, 0.36);
}

@media (max-width: $mobile-width) {
  .callback {
    background: url('../img/callback-mob.png') no-repeat top -150px center/cover;
    padding: 95px 0;
    margin-top: 0;

    & .wrapper {
      flex-wrap: wrap;
      text-align: center;
      text-shadow: 0 0 1px #000;
    }
  }

  .callback__text {
    margin-bottom: 30px;
  }

  .callback__form {
    margin-top: 20px;
  }

  .callback__text h2,
  .callback__form h3 {
    font-size: 28px;
  }
}

@media (max-width: 400px) {
  .callback {
    background-position: top center;
    padding: 125px 0 80px;
  }
}
