.about {
  background: #000 url('../img/main-bg.jpg') no-repeat top center/100% auto;
  color: #fff;
}

.about__discr {
  text-align: center;
  margin: 0;
  padding: 10px 0;
  opacity: 0.66;
}

.about__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 40px;
  margin-top: 0;
}

.about__item {
  height: 443px;
  width: 310px;
  position: relative;
  border-bottom: 3px solid $prime-color;
  transition: 0.3s;

  & .title {
    position: absolute;
    transform: rotate(-90deg);
    margin: 0;
    font-family: $title-font;
    opacity: 0.33;
  }

  &:nth-of-type(1) {
    background: url('../img/rent.png') no-repeat center/cover;

    & .title {
      font-size: 168px;
      bottom: 114px;
      right: 30px;
    }
  }

  &:nth-of-type(2) {
    background: url('../img/sell.png') no-repeat center/cover;

    & .title {
      font-size: 142px;
      bottom: 126px;
      right: 30px;
    }
  }

  &:nth-of-type(3) {
    background: url('../img/newbuild.png') no-repeat center/cover;

    & .title {
      font-size: 111px;
      bottom: 148px;
      right: 39px;
    }
  }

  .about__item-wrapper {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 20px 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: 0.3s;
  }

  & .phone {
    font-family: $title-font;
    font-size: 27px;
    align-self: flex-end;
    color: #fff;
  }

  & .about__button {
    font-family: $title-font;
    font-size: 43px;
    opacity: 0;
    color: #000;
  }

  &:hover .about__item-wrapper,
  &:focus .about__item-wrapper {
    background-color: $prime-color;
    & a {
      color: $text-color;
    }
  }

  &:hover .about__button,
  &:focus .about__button  {
    opacity: 1;
  }

  &:hover .title,
  &:focus .title  {
    transform: rotate(0);
    right: 0;
    bottom: auto;
  }

  &:nth-of-type(1):hover .title,
  &:nth-of-type(1):focus .title  {
    font-size: 120px;
    top: -29px;
  }

  &:nth-of-type(2):hover .title,
  &:nth-of-type(2):focus .title  {
    font-size: 99px;
    top: -22px;
  }

  &:nth-of-type(3):hover .title,
  &:nth-of-type(3):focus .title  {
    font-size: 77px;
    top: -15px;
  }
}

.about__title {
  font-size: 44px;
  max-width: 643px;
  margin-bottom: 30px;
}

.about__text {
  max-width: 700px;
  padding-bottom: 50px;

  & p {
    margin: 0 0 15px
  }
}

@media (max-width: 1330px) {
  .about {
    background-size: cover;

    & .wrapper {
      padding: 0 140px 0 150px;
    }
  }

  .about__list {
    justify-content: space-evenly;
  }

  .about__item {
    height: 420px;
    width: 275px;
    margin-bottom: 20px;

    & .about__button {
      font-size: 38px;
    }

    &:nth-of-type(1) .title {
      font-size: 156px;
      bottom: 113px;
      right: 16px;
    }

    &:nth-of-type(2) .title {
      font-size: 134px;
      bottom: 118px;
      right: 13px;
    }

    &:nth-of-type(3) .title {
      font-size: 105px;
      bottom: 141px;
      right: 21px;
    }

    &:nth-of-type(1):hover .title,
    &:nth-of-type(1):focus .title {
      font-size: 106px;
      top: -24px;
      right: 0;
    }

    &:nth-of-type(2):hover .title,
    &:nth-of-type(2):focus .title {
      font-size: 89px;
      top: -21px;
      right: 0;
    }

    &:nth-of-type(3):hover .title,
    &:nth-of-type(3):focus .title {
      font-size: 68px;
      top: -14px;
      right: 0;
    }
  }
}

@media (max-width: $mobile-width) {
  .about__discr {
    display: none;
  }

  .about .wrapper {
    padding: 0 20px;
  }

  .about__item {
    width: 280px;
    max-width: 100%;
    margin: 10px 0 0;
    height: 130px;
    border-bottom: none;

    &:nth-of-type(1) {
      background: url('../img/rent-mob.png') no-repeat center/cover;
    }

    &:nth-of-type(2) {
      background: url('../img/sell-mob.png') no-repeat center/cover;
    }

    &:nth-of-type(3) {
      background: url('../img/newbuild-mob.png') no-repeat center/cover;
    }

    &:nth-of-type(1) .title,
    &:nth-of-type(1):hover .title,
    &:nth-of-type(1):focus .title {
      font-size: 35px;
      bottom: 63px;
      right: auto;
      left: -25px;
      top: auto;
      transform: rotate(90deg);
    }

    &:nth-of-type(2) .title,
    &:nth-of-type(2):hover .title,
    &:nth-of-type(2):focus .title {
      font-size: 90px;
      bottom: 10px;
      right: 0;
      top: auto;
      transform: rotate(0);
    }

    &:nth-of-type(3) .title,
    &:nth-of-type(3):hover .title,
    &:nth-of-type(3):focus .title {
      font-size: 23px;
      bottom: 68px;
      right: auto;
      left: -34px;
      top: auto;
      transform: rotate(90deg);
    }

    & .about__item-wrapper {
      padding: 0;
    }

    &:hover .about__item-wrapper,
    &:focus .about__item-wrapper {
      background: transparent;
    }

    & .about__button {
      opacity: 1;
      font-size: 28px;
      background: $prime-color;
      text-align: center;
      order: 2;
    }

    & .phone {
      font-size: 21px;
      margin-bottom: 10px;
      margin-right: 10px;
    }

    &:hover .about__item-wrapper a.phone,
    &:focus .about__item-wrapper a.phone {
      color: #fff;
    }
  }

  .about__title {
    font-size: 28px;
  }

  .about__text {
    padding-bottom: 40px;
  }
}
