.header__left {
  position: absolute;
  top: 0;
  left: 0;
  width: 142px;
  background: #212121;
  box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 25px 0;
  text-align: center;
}

.left-menu__logo {
  margin-bottom: 55px;
}

#left-menu__toggle,
.left-menu__logo--mob {
  display: none;
}

.header__right {
  position: absolute;
  top: 0;
  right: 0;
  width: 105px;
  background: rgba(0, 0, 0, 0.33);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
  padding: 50px 0 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 882px;

  &.open {
    width: calc(100% - 142px);
    background: rgba(0, 0, 0, 0.5) url('../img/bg-menu.png') no-repeat center/cover;
    z-index: 10;
  }
}

@media (max-width: $mobile-width) {
  .header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    align-items: center;
    background: #212121;
    box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .left-menu__logo--desc {
    display: none;
  }

  .header__left,
  .header__right {
    position: static;
    background: none;
    box-shadow: none;
    padding: 0;
  }

  .header__left {
    display: flex;
    justify-content: space-between;
    width: 57%;
    align-items: center;

    & > a {
      order: 2;
    }
  }

  #left-menu__toggle,
  .left-menu__logo--mob {
    display: block;
    margin-bottom: 0;
  }

  .header__right {
    min-height: auto;
    width: auto;
  }
}
