.news {
  padding: 60px 0 480px;
  background: #fff url('../img/news-bg.png') center/cover;
}

.news__title {
  font-size: 44px;
  text-transform: uppercase;
  margin: 0 0 60px;

  &::after {
    content: "";
    width: 54px;
    height: 54px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    background: url('../img/news.png') no-repeat center/contain;
  }
}

.news__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.news__item {
  background: #000;
  color: #fff;
  width: 335px;
  transition: 0.3s;
  position: relative;
  margin-bottom: 10px;

  & .button--news {
    height: 0;
    overflow: hidden;
    padding: 0;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  &:hover,
  &:focus {
    border: 3px solid $prime-color;
  }

  &:hover .button--news,
  &:focus .button--news {
    height: auto;
    padding: 5px 60px 5px 10px;
  }
}

.data {
  margin-bottom: 20px;
  height: 200px;
  overflow: hidden;

  & .title {
    font-weight: 500;
    font-size: 12px;
    opacity: 0.7;
    position: absolute;
    top: 20px;
    left: 25px;
  }

  & .value {
    font-size: 22px;
    font-family: $title-font;
    position: absolute;
    top: 40px;
    left: 25px;
  }
}

.news__subtitle {
  font-size: 22px;
  font-family: $title-font;
  margin: 0;
  padding: 0 25px 35px;
}

.news__text {
  margin: 0;
  padding: 0 25px 25px;
}

@media (max-width: $mobile-width) {
  .news {
    padding: 30px 0;
  }

  .news__title {
    font-size: 28px;
    margin-bottom: 30px;

    &::after {
      width: 32px;
      height: 32px;
    }
  }

  .news__list {
    flex-wrap: nowrap;
    overflow-x: scroll;
  }

  .news__item {
    min-width: 270px;
    margin-right: 10px;

    & .button--news {
      text-align: center;
    }
  }
}
